.App {
    display: -ms-flexbox;
    display: flex;
    position: relative;
    width: 100%;
    font-size: 16px;
}

.App-mobile-logo {
    display: none;
}

.App-header {
    position: relative;
    height: 325px;
    min-height: 200px;
    max-height: 400px;
    margin: auto;
    -webkit-transition: all 0.8s;
    -o-transition: all 0.8s;
    transition: all 0.8s;
}

.App-header-first-step {
    position: relative;
    height: 325px;
    min-height: 200px;
    max-height: 400px;
    margin: auto;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
}

.App-header-last-step {
    position: relative;
    height: 325px;
    min-height: 200px;
    max-height: 400px;
    margin: auto;
    -webkit-transition: all 0.8s;
    -o-transition: all 0.8s;
    transition: all 0.8s;
}

.App-header::after {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0,0.45)), color-stop(65%, rgba(0,0,0,0.00)));
    background: -webkit-linear-gradient(bottom, rgba(0,0,0,0.45) 0%, rgba(0,0,0,0.00) 65%);
    background: -o-linear-gradient(bottom, rgba(0,0,0,0.45) 0%, rgba(0,0,0,0.00) 65%);
    background: linear-gradient(0deg, rgba(0,0,0,0.45) 0%, rgba(0,0,0,0.00) 65%);
}

.App-header-image {
    position: relative;
    height: 325px;
    min-height: 200px;
    max-height: 400px;
    padding: 20px 20px 20px 30px;
    -webkit-transition: all 0.8s;
    -o-transition: all 0.8s;
    transition: all 0.8s;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    opacity: 0;
}

.App-header-text {
    font-size: 50px;
}

.selection-panel {
    width: 55%;
    -ms-flex: 1 1;
        flex: 1 1;
}

.modal-container {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    transition: all .4s;
    z-index: 2;
}

.modal {
    position: relative;
    width: 50%;
    margin: 20vh auto;
    background-color: white;
    border-radius: 5px;
}

.modal-heading {
  text-align: center;
  padding: 20px;
}

.modal-message {
  padding: 0 10% 15%;
}

.modal-btn-container {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding-bottom: 20px;
    text-align: center;
}

@media all and (max-width: 768px) {
    .App-mobile-logo {
        display: block;
        -webkit-box-shadow: 0 7px 10px 0 rgba(0,0,0,0.06);
                box-shadow: 0 7px 10px 0 rgba(0,0,0,0.06);
    }

    .App-header-text {
        font-size: 30px;
    }

    .selection-panel {
        width: 100%;
    }

    .side-panel {
        display: none;
    }
    
    .modal {
      position: fixed;
      width: 100%;
      height: 100vh;
      margin: 0;
      border-radius: 0;
    }
}

@-webkit-keyframes scrollIn {
    0% {
        width: 0;
    }
}

@keyframes scrollIn {
    0% {
        width: 0;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
}



